<template>
  <div class="main">
    <!-- #Login Section Start-->
    <div class="row" style="background-color:#558C89; margin-right:0px">
      <div class="col-sm-6" >
        <div id="org">
          <h3>Let's Feed Faridabad</h3>
        </div>
      </div>
      <div class="col-sm-6">
        <div class="col-sm-5">
          <div class="row">
            <label for="uname" id="labels"><b>Email/Username</b></label>
          </div>
          <div class="row">
            <!-- <input type="text" id="email" placeholder="Enter your Email Address" name="email" required v-model="login.email"> -->
          <div class="input-group">
            <span class="input-group-addon"><i class="glyphicon glyphicon-user"></i></span>
            <input type="text" id="email" class="form-control" placeholder="Enter your Email Address" name="email" required v-model="login.email">
          </div>
          </div>
        </div>
        <div class="col-sm-5">
          <div class="row">
            <label for="psw" id="labels"><b>Password</b></label>
          </div>
          <div class="row">
            <!-- <input type="password" id="pwd" placeholder="Enter Password" name="pwd" required v-model="login.pwd"> -->
          <div class="input-group">
            <span class="input-group-addon"><i class="glyphicon glyphicon-lock"></i></span>
            <input type="password" id="pwd" class="form-control" placeholder="Enter Password" name="pwd" required v-model="login.pwd">
          </div>
          </div>
        </div>
        <div class="col-sm-1">
          <div class="row">
            <div class="input-group">
              <label>&nbsp;</label>
            </div>
          </div>
          <div class="row">
            <div class="input-group">
              <input type="button" value="Login" @click="loginUser" >
            </div>
          </div>
        </div>
        
      </div>
    </div>
    <!-- #Login Section End-->
    <!-- #Image and Registration Section End-->
    <!-- #Image Section End-->
    <div class="row" style="background-color:#7E8F7C;">
      <div class="col-sm-6">
        <div>
          <img src="https://firebasestorage.googleapis.com/v0/b/vuejs-firebase-35724.appspot.com/o/give.jpg?alt=media&token=d0c040ef-2774-401d-8485-5565a89dabc3"/>
        </div>
        
      </div>
      <!-- #Image Section End-->
    </div>
    <!-- #Image and Registration Section End-->
  <!-- #Footer Section End-->
  </div>   <!-- #app -->
</template>

<script>
  import firebase from 'firebase';

    var config = {
        apiKey: "AIzaSyBYxKuUZQNl79ExzBMFc8F1rys-wzW8VlI",
        authDomain: "vuejs-firebase-35724.firebaseapp.com",
        databaseURL: "https://vuejs-firebase-35724.firebaseio.com",
        projectId: "vuejs-firebase-35724",
        storageBucket: "vuejs-firebase-35724.appspot.com",
        messagingSenderId: "678876741247"
    };
    firebase.initializeApp(config);

  export default {
        data() {
            return {   
                login: {
                    email: '',
                    pwd: ''
                },
                newUser: {
                    email: '',
                    pwd: '',
                    pwdConfirm: ''
                }
            }
        },
        mounted: function() {
            
        },
        methods: {
            loginUser: function() {
                var email = this.login.email,
                    password = this.login.pwd,
                    me = this;

                if(email === '' || pwd === '') {
                    alert("Please fill all the fields before submmiting the form");
                } else {
                    firebase.auth().signInWithEmailAndPassword(email, password).then(function(firebaseUser) {
                        me.$router.push({name: 'EventDetail'}); 
                    })
                    .catch(function(error) {
                        var errorMessage = error.message;
                        alert(errorMessage);
                    });
                }
            }
        }
    }

</script>

<style>
/* #app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
} */
#org, org2 {
  text-align: center;
  color: white;
}

#labels {
  text-align: left;
  color: #558C89;
}

input[type=button]:hover{
    background: #EA7B00;
    color: #fff;
}

img {
    max-width: 100%;
    height: auto;
    width: auto\9; /* ie8 */
}

.main {
  height: 100%;
	margin-right: auto;
	margin-left: auto;
}

.row {
	margin-right:0px;
	margin-left:-15px
}

</style>



