import Vue from 'vue'
import vueFire from 'vuefire'
import App from './App'
import Router from 'vue-router'
import Home from './components/Home'
import RegisterUser from './components/UserRegistration'
import Login from './components/Login'
import EventDetail from './components/EventDetail'
import firebase from 'firebase';

Vue.use(vueFire)
Vue.use(Router)   //register plugin
const routes = [          //----------------------- 1
    { 
      path: '*',    
      redirect: Home
    },
    { 
      path: '/',    //localhost:8080/
      component: Home, 
      name: "Home"
    },
    { 
       path: '/register',    //localhost:8080/register
       component: RegisterUser, 
       name: "UserRegistration"
     },
     { 
        path: '/login',    //localhost:8080/login
        component: Login, 
        name: "Login"
      },
      { 
        path: '/eventDetail',    //localhost:8080/login
        component: EventDetail, 
        name: "EventDetail",
        meta: {
          requiresAuth: true
        }
      }
  ];


const myRoutes = new Router({
  routes: routes //vue router takes an array of configs containing path-component mappings--This is ref to 10 line
});

const config = {
  apiKey: "AIzaSyBYxKuUZQNl79ExzBMFc8F1rys-wzW8VlI",
  authDomain: "vuejs-firebase-35724.firebaseapp.com",
  databaseURL: "https://vuejs-firebase-35724.firebaseio.com",
  projectId: "vuejs-firebase-35724",
  storageBucket: "gs://vuejs-firebase-35724.appspot.com",
  messagingSenderId: "678876741247"
}
var app1 = firebase.initializeApp(config,'main1');

myRoutes.beforeEach((to, from, next) => {
  var currentUser;
    var firebaseObj = Object.keys(window.localStorage)
    .filter(item => item.startsWith('firebase:authUser'))[0];
    var firebaseParser = JSON.parse(localStorage.getItem(firebaseObj));
    if(firebaseParser) {
      currentUser = firebaseParser.uid;
    }
    
    var requiresAuth = to.matched.some(record => record.meta.requiresAuth);
    if(requiresAuth && !currentUser) next('/')
      else if(!requiresAuth && currentUser) next('EventDetail')
      else next()
  
})
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router: myRoutes,
  components: { App },
  template: '<App/>'
})







